			<p>10.2 甲、乙双方均须确认，本协议的签订、生效和履行以不违反中国的法律法规为前提。如果本协议中的任何一条或多条条款违反相关法律法规，则相关条款被视为无效，但相关无效条款不影响本协议其他条款的效力。</p>
